import { headers } from '@/consts'
import request from '../request'
import {AddCommonUse, AddCommonUsePublish} from "@/api/model/teamModel";

/**
 * 获取我的常用
 * @param params
 */
export function fetchCommonUseList(params: { teamId: string,sortType: number,search?: string }) {
  return request({
    url: `/api/v1/team/nav/getCommonUseList`,
    method: 'get',
    params,
    headers
  })
}

