<template>
  <div class="folder-tree" id="folder-tree">
    <el-tree
      empty-text="暂无数据"
      accordion
      :data="treeData"
      :props="defaultProps"
      @node-click="handleNodeClick"
    >
      <template #default="{ node, data }">
        <div class="custom-tree-node w-full flex items-center justify-between" style="cursor: not-allowed" v-if="data.disable" @click.stop>
					<span class="flex items-center">
						<SvgIcon
              :name="getIcon(data)"
              :size="18"
              class="mr-1"
            />
						<span>{{ node.label }}</span></span
          >
          <span v-if="data.value === currentFolderValue" class="gray-text"> 当前目录 </span>
        </div>
        <div class="custom-tree-node w-full flex items-center justify-between" v-else>
					<span class="flex items-center">
						<SvgIcon
              :name="getIcon(data)"
              :size="18"
              class="mr-1"
            />
						<span>{{ node.label }}</span></span
          >
          <span v-if="data.value === currentFolderValue" class="gray-text"> 当前目录 </span>
        </div>
      </template>
    </el-tree>
  </div>
  <div class="flex justify-end mt-10" v-if="!isCollect">
    <el-button type="primary" @click="handleClone">复制</el-button>
    <el-button type="primary" :disabled="moveDisabled" @click="handleMove">移动</el-button>
    <el-button @click="handleClose">取消</el-button>
  </div>
  <div class="flex justify-end mt-10" v-if="isCollect">
    <el-button type="primary" @click="handleCollect">收藏</el-button>
    <el-button @click="handleClose">取消</el-button>
  </div>
</template>

<script setup lang="ts">
import {computed, reactive, ref, unref} from 'vue'
import {ElMessage, ElTree} from 'element-plus'
import {fetchClone, fetchMove} from '@/api/fetchs/files'
import {FileCloneMove} from '@/api/model/filesModel'
import {useEmit, useOnEmitFn} from '@/utils/useEmitter'
import {getSearchParams} from '@/utils/form'
import {fetchCollect} from '@/api/fetchs/nav'
import {useTeamStore} from "@/store/modules/team";
import {fetchAllDirTree, fetchDirTree} from "@/api/fetchs/teamNav";
import {useFilesStore} from "@/store/modules/files";
import {useRouter} from "vue-router";
import {RouteEnum} from "@/enums/routeEnum";

interface FolderTree {
  name: string
  children?: FolderTree[]
  dirId: string
  value: string
  spaceId: string
}

const props = defineProps<{
  dataIds?: string[]
  isCollect?: boolean //是否是收藏
  itemId?: string //收藏的item id
  showAllDir?: boolean //是否显示该用户下所有团队的目录
}>()
const defaultProps = {
  children: 'children',
  label: 'label'
}
const treeData = ref<FolderTree[]>([])

const teamStore = useTeamStore()
const filesStore = useFilesStore()
const { currentRoute } = useRouter()

const currentFolderValue = computed(() => {
  // 只在个人空间&团队空间页显示当前目录
  const showCurrentDirPaths = [
    `${RouteEnum.SELF_FILE}/${RouteEnum.HIDE_CHILDREN_IN_MENU}`,
    `${RouteEnum.TEAM}/${RouteEnum.HIDE_CHILDREN_IN_MENU}`,
  ]
  if(showCurrentDirPaths.indexOf(unref(currentRoute).path) < 0) {
    return ''
  }

  let breadcrumb = filesStore.getFolderBreadcrumb
  if (breadcrumb && breadcrumb.length > 0) {
    return breadcrumb[breadcrumb.length - 1].id
  }
  return ''
})

const form = reactive<FileCloneMove>({
  folderId: '',
  spaceId: '',
  dataIds: props.dataIds
})
const moveDisabled = computed(() => props.dataIds?.includes(form.folderId as string))

const emit = defineEmits(['formResponse'])

function handleClone() {
  handleSaveCloneApi()
  emit('formResponse', true)
}

function getIcon(data) {
  if (data.dirId === 'root') {
    return data.spaceType == 'MINE' ? 'menu-self-file' : data.spaceType == 'PUBLIC' ? 'space-public' : 'space-private'
  } else {
    return 'list-folder'
  }
}

function handleMove() {
  handleSaveMoveApi()
  emit('formResponse', false)
}

function handleClose() {
  emit('formResponse', false)
}

function handleCollect() {
  if(!form.folderId) {
    ElMessage.warning('请选择目录')
    return
  }
  handleSaveCloneApi('收藏成功')
  emit('formResponse', true)
}

function handleNodeClick(data: FolderTree) {
  form.folderId = data.dirId
  form.spaceId = data.spaceId
}

/* ================================== fetchs ================================== */
async function handleGetTreeData() {
  const {data} = props.showAllDir ? await fetchAllDirTree() : await fetchDirTree({teamId: teamStore.getActiveTeamId})
  treeData.value = data
}

handleGetTreeData()

useOnEmitFn('handleUpdateTreeData', handleGetTreeData)

async function handleSaveCloneApi(msg = '复制成功') {
  const {status} = await fetchClone(form)
  if (status == 1) {
    // @ts-ignore
    ElMessage.success(msg)
    useEmit('handleUpdateList')
    useEmit('handleUpdateMultipleList')
  }
}

async function handleSaveMoveApi() {
  await fetchMove(form)
  // @ts-ignore
  ElMessage.success('移动成功')
  useEmit('handleUpdateList')
  useEmit('handleUpdateMultipleList')
}

async function handleCollectApi() {
  console.log('form:', form)
  const params = getSearchParams({
    id: props.itemId,
    pid: form.folderId || 'root'
  })
  const {status} = await fetchCollect(params)
  if (status == 1) {
    ElMessage.success('收藏成功')
    useEmit('handleUpdateList')
  }
}
</script>

<style lang="scss">
.folder-tree {
  height: 50vh;
  overflow-y: auto;

  .custom-tree-node {
    height: 30px;
  }

  .gray-text {
    color: #ccc;
    float: right;
    padding-right: 5px;
  }

  .el-tree-node__content {
    height: 45px;
  }
}
</style>
