import { useEmit, useOffEmitFn } from '@/utils/useEmitter'
import { reactive } from 'vue'

export function useCloneMoveForm(title = '移动/复制到') {
	const cloneMoveData = reactive({
		visible: false,
		title
	})

	function handleCloneMoveVisible() {
		cloneMoveData.visible = true
		useOffEmitFn('handleUpdateTreeData')
		useEmit('handleUpdateTreeData')
	}
	// 文件夹操作
	function handleCloneMoveRes(val) {
		if (val) {
			console.log('操作成功后...')
		}
		cloneMoveData.visible = false
	}

	return {
		cloneMoveData,
		handleCloneMoveVisible,
		handleCloneMoveRes
	}
}
