import { headers } from '@/consts'
import {
	FileCloneMove,
	FileFolder,
	FileIds,
	FileLoadDatas,
	FileSort,
	FileWebsite
} from '../model/filesModel'
import request from '../request'

/* 复制和克隆 */
export function fetchClone(data: FileCloneMove) {
	return request({
		url: `/api/v1/my/data/clone`,
		method: 'post',
		data
	})
}
/* 获取用户目录树 */
export function fetchDirTree(spaceId) {
	return request({
		url: `/api/v1/my/data/getDirTree`,
		method: 'get',
    params: {spaceId},
		headers
	})
}
/* 抓取网站信息 */
export function fetchWebsiteInfo(url: string) {
	return request({
		url: `/api/v1/my/data/getWebsiteInfo`,
		method: 'get',
		params: { url }
	})
}
/* 加载数据 */
export function fetchList(params: FileLoadDatas) {
	return request({
		url: `/api/v1/my/data/loadDatas`,
		method: 'get',
		params,
		headers
	})
}
/* 移动 */
export function fetchMove(data: FileCloneMove) {
	return request({
		url: `/api/v1/my/data/move`,
		method: 'post',
		data
	})
}
/* 清空回收站 */
export function fetchClearAll() {
	return request({
		url: `/api/v1/my/data/removeAllFromTrash`,
		method: 'post',
		headers
	})
}
/* 彻底删除 */
export function fetchDeleteCompletely(data: FileIds) {
	return request({
		url: `/api/v1/my/data/removeFromTrash`,
		method: 'post',
		data
	})
}
/* 还原 */
export function fetchRestore(data: { id: string }) {
	return request({
		url: `/api/v1/my/data/restore`,
		method: 'post',
		data,
		headers
	})
}
/* 保存目录 */
export function fetchSaveFolder(data: FileFolder) {
	return request({
		url: `/api/v1/my/data/saveFolder`,
		method: 'post',
		data,
		headers
	})
}
/* 拖拽排序 */
export function fetchSaveSort(data: FileSort) {
	return request({
		url: `/api/v1/my/data/saveSort`,
		method: 'post',
		data
	})
}
/* 保存网站 */
export function fetchSaveWebsite(data: FileWebsite) {
	return request({
		url: `/api/v1/my/data/saveWebsite`,
		method: 'post',
		data,
		headers
	})
}
/* 删除移入回收站 */
export function fetchTrash(data: FileIds) {
	return request({
		url: `/api/v1/my/data/toTrash`,
		method: 'post',
		data
	})
}

/* 上传网站logo */
export function fetchUploadImage(formData) {
	return request({
		url: `/api/v1/file/uploadUserWebsiteLogo`,
		method: 'post',
		data: formData,
		headers: {
			'Content-Type': 'multipart/form-data' //修改请求头
		}
	})
}

/* 获取文件数量 */
export function fetchFileCount(teamId: string) {
	return request({
		url: `/api/v1/my/data/getFileCount`,
		method: 'get',
    params: { teamId },
		headers
	})
}
